body {
  background-image: linear-gradient(120deg, #f6d365 0%, #fda085 100%);
  display: flex;
  justify-content: center;
  align-items: center;
}

.app {
  margin-top: 150px;
}

.app input {
  border: 0;
  outline: 0;
  padding: .5rem 1rem;
  font-size: 2rem;
  transition: background-color .5s, color .5s, border .5s;
}

.app input[type='submit'] {
  color: black;
  background-color: white;
  cursor: pointer;
  border: 1px solid tomato;

}

.app input[type='submit']:hover {
  background-color: tomato;
  border: 1px solid transparent;
}

.items-list {
  margin-top: 30px;
}

.item,
.main-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 5px;
  background-color: white;
  padding: .5rem;
  font-size: 2rem;
}

.item .delete {
  color: white;
  background-color: tomato;
  cursor: pointer;
  padding: .5rem;
  font-size: 2rem;
  border: 0;
  outline: 0;
  transition: all .5s;
}

.item .done {
  color: white;
  background-color: greenyellow;
  cursor: pointer;
  padding: .5rem;
  font-size: 2rem;
  border: 0;
  outline: 0;
  transition: all .5s;

}

.item .delete:hover {
  background-color: rgb(228, 61, 32);
}

.main-item {
  justify-content: space-around;

}